import { Component } from "react";
import styled from "styled-components";

class BingoCell extends Component {
  render() {
    const { selectingNumber, number, bingoSet, activeklass } = this.props;
    return (
      <div>
        <Button activeklass={activeklass} onClick={bingoSet.turn ? selectingNumber : null}>
          {number}
        </Button>
      </div>
    );
  }
}

const Button = styled.button`
  width: 20%;
  height: 20%;
  float: left;
  border: 1px solid #333;
  background-color: ${props => {
    if (props.activeklass) return "#287ac6";
  }};
  outline: none;
  color: ${props => {
    if (props.activeklass) return "#fff";
  }};
`;

export default BingoCell;
